import { Color } from "../Math/Color";
import { Matrix4x4 } from "../Math/Matrix4x4";
import { Vector2 } from "../Math/Vector2";
import { Vector3 } from "../Math/Vector3";
import { Vector4 } from "../Math/Vector4";
import { CubeMap } from "../Resources/CubeMap";
import { Texture } from "../Resources/Texture";

// 
export type AttributeType = number | Color | Vector2 | Vector3 | Vector4 | Matrix4x4 | Texture | CubeMap | string;

// 
export interface VertexAttributes {
    [key: string]: AttributeType;
}

export interface ShaderRenderState {
    colorMask?: ColorMask;           //  All
    cullMode?: CullMode;             //  Back
    zTest?: ZTest;                   //  LEqual
    zWrite?: boolean;                //  true
    blend?: Blend;                   // 
    stencil?: Stencil;               // 
}

export interface Blend {
    op?: BlendOp;            //  Add
    src: BlendFactor;
    dst: BlendFactor;
    // UnityRGBA
    // srcRGB?: BlendFactor;
    // dstRGB?: BlendFactor;
    // srcAlpha?: BlendFactor;
    // dstAlpha?: BlendFactor;
}

export interface Stencil {
    /*
    Ref:GPU  compareOperation 
     readMask  writeMask 
     PassFail  ZFail  Replace GPU 
    */
    ref?: number;             // 0  255 0
    readMask?: number;        // GPU 0  255 255
    writeMask?: number;       // GPU 0  255 255
    comparisonOperation?: StencilCompareFunction;   // GPU  Always
    passOperation?: StencilOp;      // GPU  Keep
    failOperation?: StencilOp;      // GPU  Keep
    zFailOperation?: StencilOp;     // GPU  Keep
}

export enum BlendFactor {
    One,            //  one
    Zero,           //  zero
    SrcColor,       // GPU 
    SrcAlpha,       // GPU  Alpha 
    DstColor,       // GPU 
    DstAlpha,       // GPU  Alpha 
    OneMinusSrcColor,       // GPU 1 - 
    OneMinusSrcAlpha,       // GPU 1 -  Alpha
    OneMinusDstColor,       // GPU 1 - 
    OneMinusDstAlpha,       // GPU 1 -  Alpha
}

export enum BlendOp {
    Add,    // 
    Sub,    // 
    RevSub, // 
    Min,    // 
    Max,    // 
}

export enum RenderType {
    Opaque,
    Transparent
}

export enum ColorMask {
    None = 0,
    Red = 1,
    Green = 2,
    Blue = 4,
    Alpha = 8,
    All = Red | Green | Blue | Alpha
}

export enum CullMode {
    Off = 0,
    Front = 1,
    Back = 2
}

export enum StencilCompareFunction {
    Never = 1,	    // 
    Less = 2,	    // 
    Equal = 3,	    // 
    LEqual = 4,	    // 
    Greater = 5,	// 
    NotEqual = 6,	// 
    GEqual = 7,	    // 
    Always = 8,	    // 
}

export enum StencilOp {
    Keep = 0,	    // 
    Zero = 1,	    //  0 
    Replace = 2,	// 
    IncrSat = 3,	//  255 255
    DecrSat = 4,	//  0 0
    Invert = 5,	    // 
    IncrWrap = 7,	//  255 0
    DecrWrap = 8,	//  0 255
}

export enum ZTest {
    Less,	    // 
    LEqual,	    // 
    Equal,	    // 
    GEqual,	    // 
    Greater,	// 
    NotEqual,	// 
    Always,	    // 
}

// 
export const StencilPresets = {
    // 
    frontOnly(ref: number): Partial<Stencil> {
        return {
            comparisonOperation: StencilCompareFunction.Equal,
            ref: ref,
            passOperation: StencilOp.Keep,
            failOperation: StencilOp.Zero,
            zFailOperation: StencilOp.Keep
        }
    },

    // 
    outline(ref: number): Partial<Stencil> {
        return {
            comparisonOperation: StencilCompareFunction.NotEqual,
            ref: ref,
            passOperation: StencilOp.Keep,
            failOperation: StencilOp.Keep,
            zFailOperation: StencilOp.Keep
        }
    },

    // 
    accumulate(ref: number): Partial<Stencil> {
        return {
            comparisonOperation: StencilCompareFunction.Always,
            ref: ref,
            passOperation: StencilOp.IncrSat,
            failOperation: StencilOp.Keep,
            zFailOperation: StencilOp.Keep
        }
    },
};

/**
 * 
 * @param z 
 * @param currentDepth 
 * @param zTestFunc ZTest 
 * @returns 
 */
export function depthTest(z: number, currentDepth: number, zTestFunc: ZTest = ZTest.LEqual): boolean {
    switch (zTestFunc) {
        case ZTest.Less:
            return z < currentDepth; // 
        case ZTest.Equal:
            return Math.abs(z - currentDepth) < 1e-6; // 
        case ZTest.LEqual:
            return z <= currentDepth; // 
        case ZTest.Greater:
            return z > currentDepth; // 
        case ZTest.NotEqual:
            return Math.abs(z - currentDepth) >= 1e-6; // 
        case ZTest.GEqual:
            return z >= currentDepth; // 
        case ZTest.Always:
            return true; // 
        default:
            console.warn("Unknown ZTest function, using Less as default.");
            return z < currentDepth;
    }
}

/**
 * 
 * @param currentValue 
 * @param refValue 
 * @param func 
 * @param readMask 
 * @returns 
 */
export function stencilTest(
    currentValue: number,
    refValue: number = 0,
    func: StencilCompareFunction = StencilCompareFunction.Always,
    readMask: number = 0xFF
): boolean {
    // 
    const maskedCurrent = currentValue & readMask;
    const maskedRef = refValue & readMask;

    switch (func) {
        case StencilCompareFunction.Never:
            return false;
        case StencilCompareFunction.Less:
            return maskedCurrent < maskedRef;
        case StencilCompareFunction.Equal:
            return maskedCurrent === maskedRef;
        case StencilCompareFunction.LEqual:
            return maskedCurrent <= maskedRef;
        case StencilCompareFunction.Greater:
            return maskedCurrent > maskedRef;
        case StencilCompareFunction.NotEqual:
            return maskedCurrent !== maskedRef;
        case StencilCompareFunction.GEqual:
            return maskedCurrent >= maskedRef;
        case StencilCompareFunction.Always:
            return true;
        default:
            return false;
    }
}

/**
 * 
 * @param currentValue 
 * @param refValue 
 * @param op 
 * @param writeMask 
 * @returns 
 */
export function applyStencilOperation(
    currentValue: number,
    refValue: number = 0,
    op: StencilOp = StencilOp.Keep,
    writeMask: number = 0xFF
): number {
    let newValue = currentValue;

    switch (op) {
        case StencilOp.Keep:
            // 
            newValue = currentValue;
            break;
        case StencilOp.Zero:
            // 0
            newValue = 0;
            break;
        case StencilOp.Replace:
            // 
            newValue = refValue;
            break;
        case StencilOp.IncrSat:
            // 255
            newValue = Math.min(currentValue + 1, 255);
            break;
        case StencilOp.DecrSat:
            // 0
            newValue = Math.max(currentValue - 1, 0);
            break;
        case StencilOp.Invert:
            // 8
            newValue = (~currentValue) & 0xFF;
            break;
        case StencilOp.IncrWrap:
            // 2550
            newValue = (currentValue + 1) % 256;
            break;
        case StencilOp.DecrWrap:
            // 0255
            newValue = (currentValue - 1 + 256) % 256;
            break;
    }

    // 
    return (newValue & writeMask) | (currentValue & ~writeMask);
}

export function applyColorMask(color: Color, bufferColor: Color, mask: ColorMask) {
    color.r = (mask & ColorMask.Red) ? color.r : bufferColor.r;
    color.g = (mask & ColorMask.Green) ? color.g : bufferColor.g;
    color.b = (mask & ColorMask.Blue) ? color.b : bufferColor.b;
    color.a = (mask & ColorMask.Alpha) ? color.a : bufferColor.a;
}

export function blendColors(srcColor: Color, dstColor: Color, srcFactor: BlendFactor, dstFactor: BlendFactor, blendOp: BlendOp = BlendOp.Add): Color {
    // finalValue = sourceFactor * sourceValue operation destinationFactor * destinationValue
    //  =    []   

    // 
    const [srcRFactor, srcGFactor, srcBFactor, srcAFactor] = getBlendFactorValues(srcColor, dstColor, srcFactor);
    const [dstRFactor, dstGFactor, dstBFactor, dstAFactor] = getBlendFactorValues(srcColor, dstColor, dstFactor);

    // 
    let r = calculateBlendValue(srcColor.r, dstColor.r, srcRFactor, dstRFactor, blendOp);
    let g = calculateBlendValue(srcColor.g, dstColor.g, srcGFactor, dstGFactor, blendOp);
    let b = calculateBlendValue(srcColor.b, dstColor.b, srcBFactor, dstBFactor, blendOp);
    let a = calculateBlendValue(srcColor.a, dstColor.a, srcAFactor, dstAFactor, blendOp);

    // 0-1
    return new Color(r, g, b, a);
}

/**
 * 
 */
function getBlendFactorValues(src: Color, dst: Color, factor: BlendFactor): [number, number, number, number] {
    switch (factor) {
        case BlendFactor.One:
            return [1, 1, 1, 1];
        case BlendFactor.Zero:
            return [0, 0, 0, 0];
        case BlendFactor.SrcColor:
            return [src.r, src.g, src.b, src.a];
        case BlendFactor.SrcAlpha:
            return [src.a, src.a, src.a, src.a];
        case BlendFactor.DstColor:
            return [dst.r, dst.g, dst.b, dst.a];
        case BlendFactor.DstAlpha:
            return [dst.a, dst.a, dst.a, dst.a];
        case BlendFactor.OneMinusSrcColor:
            return [1 - src.r, 1 - src.g, 1 - src.b, 1 - src.a];
        case BlendFactor.OneMinusSrcAlpha:
            return [1 - src.a, 1 - src.a, 1 - src.a, 1 - src.a];
        case BlendFactor.OneMinusDstColor:
            return [1 - dst.r, 1 - dst.g, 1 - dst.b, 1 - dst.a];
        case BlendFactor.OneMinusDstAlpha:
            return [1 - dst.a, 1 - dst.a, 1 - dst.a, 1 - dst.a];
        default:
            return [0, 0, 0, 0];
    }
}

/**
 * 
 */
function calculateBlendValue(src: number, dst: number, srcFactor: number, dstFactor: number, op: BlendOp): number {
    const srcVal = src * srcFactor;
    const dstVal = dst * dstFactor;

    switch (op) {
        case BlendOp.Add:
            return srcVal + dstVal;
        case BlendOp.Sub:
            return srcVal - dstVal;
        case BlendOp.RevSub:
            return dstVal - srcVal;
        case BlendOp.Min:
            return Math.min(srcVal, dstVal);
        case BlendOp.Max:
            return Math.max(srcVal, dstVal);
        default:
            return srcVal;
    }
}